import React, { useState } from "react"
import { Navbar, NavbarBrand, Nav, NavItem, Collapse, NavLink } from "reactstrap"
import { Link } from "react-router-dom"

export default () => {
  const [isOpen, setIsOpen] = useState(false)
  const toggle = () => setIsOpen(!isOpen)

  return (
    <div>
      <Navbar color="light" light expand="md" fixed="top">
        <Link to={"/" + process.env.PUBLIC_URL + "/"}>
          <NavbarBrand>Kindly</NavbarBrand>
        </Link>
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto" navbar>
            <NavItem>
              <Link to={"/" + process.env.PUBLIC_URL + "/iwanttohelp"}>
                <NavLink>Nabízím pomoc</NavLink>
              </Link>
            </NavItem>
            <NavItem>
              <Link to={"/" + process.env.PUBLIC_URL + "/ineedhelp"}>
                <NavLink>Žádám o pomoc</NavLink>
              </Link>
            </NavItem>
            <NavItem>
              <Link to={"/" + process.env.PUBLIC_URL + "/feedback"}>
                <NavLink>Kontakt</NavLink>
              </Link>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  )
}
